.hero {
  margin: 0 20px;
  border-radius: 40px;
  margin-bottom: 72px;
  background: linear-gradient(
    to bottom,
    rgba(17, 17, 17, 1),
    rgba(17, 17, 17, 0.4)
  );
  background-image: linear-gradient(
      to bottom,
      rgba(17, 17, 17, 0) 0%,
      rgba(17, 17, 17, 0.4) 100%
    ),
    url(../images/hero-m.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media screen and (min-width: 768px) {
    background-image: unset;
  }

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        to bottom,
        rgba(17, 17, 17, 0) 0%,
        rgba(17, 17, 17, 0.4) 100%
      ),
      url(../images/hero-m@2x.jpg);
  }

  @media screen and (min-width: 768px) {
    background: linear-gradient(
      to bottom,
      rgba(17, 17, 17, 1),
      rgba(17, 17, 17, 0.4)
    );
    background-image: linear-gradient(
        to bottom,
        rgba(17, 17, 17, 0) 0%,
        rgba(17, 17, 17, 0.4) 100%
      ),
      url(../images/hero-t.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to bottom,
          rgba(17, 17, 17, 0) 0%,
          rgba(17, 17, 17, 0.4) 100%
        ),
        url(../images/hero-t@2x.jpg);
    }
  }

  @media screen and (min-width: 1200px) {
    background: linear-gradient(
      to bottom,
      rgba(17, 17, 17, 1),
      rgba(17, 17, 17, 0.4)
    );
    background-image: linear-gradient(
        to bottom,
        rgba(17, 17, 17, 0) 0%,
        rgba(17, 17, 17, 0.4) 100%
      ),
      url(../images/hero-d.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to bottom,
          rgba(17, 17, 17, 0) 0%,
          rgba(17, 17, 17, 0.4) 100%
        ),
        url(../images/hero-d@2x.jpg);
    }
  }
}

.menu {
  padding: 320px 20px 20px;

  @media screen and (min-width: 768px) {
    padding: 611px 40px 40px;
  }

  @media screen and (min-width: 1200px) {
    padding: 511px 40px 40px;
  }
}

.hero-title {
  width: 100%;
  color: #fff;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.6px;
  margin-bottom: 32px;

  @media screen and (min-width: 768px) {
    width: 484px;
    height: 102px;
    font-size: 28px;
    line-height: 34px;
    letter-spacing: -0.84px;
  }
}

.hero-button {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }

  @media screen and (min-width: 1200px) {
    display: flex;
    align-items: center;
  }
}

.scroll-wrapper {
  display: flex;
  align-items: center;
}

.scroll-text {
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  letter-spacing: -0.36px;
  margin-right: 14px;

  @media screen and (min-width: 1200px) {
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.42px;
  }
}

.button-wrapper {
  font-family: 'Montserrat';
  margin-bottom: 32px;
  width: auto;
  display: flex;
  justify-content: space-between;

  @media screen and (min-width: 473px) {
    margin-bottom: 0;
    width: auto;
  }
}

.buy-button {
  color: var(--white);
  background-color: var(--orange);
  padding: 12px 28px;
  border-radius: 100px;
  margin-right: 14px;
  border-color: transparent;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 1200px) {
    padding: 14px 40px;
  }
}

.buy-button:hover,
.buy-button:focus {
  color: var(--orange);
  background-color: #fcf1e0;
}

.made-button {
  color: var(--white);
  background-color: transparent;
  padding: 12px 28px;
  border: 1px solid #fff;
  border-radius: 100px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    padding: 14px 40px;
  }
}

.made-button:hover,
.made-button:focus {
  color: var(--orange);
  border: 1px solid var(--orange);
}

.scroll-button {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--orange);
  background: #fff;
  border-color: transparent;

  @media screen and (min-width: 1200px) {
    width: 38px;
    height: 38px;
  }
}

.scroll-icon {
  fill: currentColor;
}

.scroll-wrapper {
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.scroll-wrapper:hover,
.scroll-wrapper:focus {
  .scroll-text {
    color: var(--orange);
  }
  .scroll-button {
    color: var(--white);
    background: var(--orange);
  }
}
